<template>
  <x-header>
    单位换算
  </x-header>
  <div class="unit-show-box">
    <div class="unit-show-screen">
      <div class="unit-show-screen-left">
        <form @submit="conversionOnLeft">
          <input type="text" v-model="currentLeftValue" size="5" style="text-align:right;"> {{currentLeftUnit.name}}
        </form>
      </div>
      <div class="" style="width:40px;padding-top:32px;">
        <img src="../assets/equal.png" />
      </div>
      <div class="unit-show-screen-right">
        <form @submit="conversionOnRight">
          <input type="text" v-model="currentRightValue" size="5" style="text-align:right;"> {{currentRightUnit.name}}
        </form>
      </div>
    </div>
  </div>
  <div class="unit-show-list-box">
    <div class="unit-show-list-screen">
      <div class="unit-show-list">
        <div class="unit-selected"></div>
        <ul class="unit-show-list-item" v-touch:panup="unitPanUpOnLeft" v-touch:pandown="unitPanDownOnLeft">
          <li v-for="unit in showedUnitsOnLeft">
            {{unit.name}}
          </li>
        </ul>
      </div>
      <div style="width:40px;">
        <div class="unit-selected">
          <img src="../assets/topdownarrow.png" style="margin-top:22px;" />
        </div>
      </div>
      <div class="unit-show-list">
        <div class="unit-selected"></div>
        <ul class="unit-show-list-item" v-touch:panup="unitPanUpOnRight" v-touch:pandown="unitPanDownOnRight">
          <li v-for="unit in showedUnitsOnRight">
            {{unit.name}}
          </li>
        </ul>
      </div>
    </div>
  </div>
  <footer>
    <div class="unit-tabbar">
      <div class="unit-tabbar-item" @click="showUnits(1)" :class="{ 'active': unitTab == 1 }">
        长度
      </div>
      <div class="unit-tabbar-item" @click="showUnits(2)" :class="{ 'active': unitTab == 2 }">
        面积
      </div>
      <div class="unit-tabbar-item" @click="showUnits(3)" :class="{ 'active': unitTab == 3 }">
        重量
      </div>
      <div class="unit-tabbar-item" @click="showUnits(4)" :class="{ 'active': unitTab == 4 }">
        体积
      </div>
      <div class="unit-tabbar-item" @click="showUnits(5)" :class="{ 'active': unitTab == 5 }">
        温度
      </div>
    </div>
  </footer>
</template>
<script>
  import XHeader from 'vux/components/x-header'
  import Scroller from 'vux/components/scroller'

  export default {
    data () {
      return {
        leftOptions: {
          showBack: true,
          backText: 'Back',
          preventGoBack: true
        },
        currentLeftValue: 0.0,
        currentLeftUnit: { name: '海里', value: '' },
        currentRightValue: 0.0,
        currentRightUnit: { name: '海里', value: '' },
        unitTab: 1,
        showedUnitsOnLeft: [
          { name: '海里', value: '' },
          { name: '英尺', value: '' },
          { name: '英里', value: '' },
          { name: '英时', value: '' }
        ],
        showedUnitsOnRight: [
          { name: '海里', value: '' },
          { name: '英尺', value: '' },
          { name: '英里', value: '' },
          { name: '英时', value: '' }
        ],
        lengthUnits: [
          { name: '海里', value: '' },
          { name: '英尺', value: '' },
          { name: '英里', value: '' },
          { name: '英时', value: '' }
        ],
        areaUnits: [
          { name: '平方米', value: '' },
          { name: '平方厘米', value: '' },
          { name: '平方分米', value: '' }
        ],
        weightUnits: [
          { name: '千克', value: '' },
          { name: '克', value: '' },
          { name: '顿', value: '' }
        ],
        volumeUnits: [
          { name: '立方米', value: '' },
          { name: '立方分米', value: '' }
        ],
        temperatureUnits: [
          { name: '摄氏度', value: '' },
          { name: '华氏度', value: '' }
        ]
      }
    },
    route: {
      data (transition) {
      }
    },
    computed: {
    },
    methods: {
      showUnits (tab) {
        this.unitTab = tab
        switch (this.unitTab) {
          case 1:
            this.showedUnitsOnLeft = this.lengthUnits
            this.showedUnitsOnRight = this.lengthUnits
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 2:
            this.showedUnitsOnLeft = this.areaUnits
            this.showedUnitsOnRight = this.areaUnits
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 3:
            this.showedUnitsOnLeft = this.weightUnits
            this.showedUnitsOnRight = this.weightUnits
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 4:
            this.showedUnitsOnLeft = this.volumeUnits
            this.showedUnitsOnRight = this.volumeUnits
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 5:
            this.showedUnitsOnLeft = this.temperatureUnits
            this.showedUnitsOnRight = this.temperatureUnits
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          default:
            this.showedUnitsOnLeft = this.lengthUnits
            this.showedUnitsOnRight = this.lengthUnits
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
        }
      },
      unitPanUpOnLeft () {
        this.showedUnitsOnLeft.push(this.showedUnitsOnLeft.splice(0, 1)[0])
        this.currentLeftUnit = this.showedUnitsOnLeft[0]
      },
      unitPanDownOnLeft () {
        this.showedUnitsOnLeft.unshift(this.showedUnitsOnLeft.splice(this.showedUnitsOnLeft.length - 1, 1)[0])
        this.currentLeftUnit = this.showedUnitsOnLeft[0]
      },
      unitPanUpOnRight () {
        this.showedUnitsOnRight.push(this.showedUnitsOnRight.splice(0, 1)[0])
        this.currentRightUnit = this.showedUnitsOnRight[0]
      },
      unitPanDownOnRight () {
        this.showedUnitsOnRight.unshift(this.showedUnitsOnRight.splice(this.showedUnitsOnRight.length - 1, 1)[0])
        this.currentRightUnit = this.showedUnitsOnRight[0]
      },
      conversionOnLeft (e) {
        e.preventDefault()
        alert('左边输入根据this.currentLeftUnit.value(' + this.currentLeftUnit.value +
              ')和this.currentRightUnit.value(' + this.currentRightUnit.value +
              ')搜索出转换参数，然后把this.currentLeftValue(' + this.currentLeftValue +
              ')和this.currentRightValue(' + this.currentRightValue +
              ')进行换算')
      },
      conversionOnRight (e) {
        e.preventDefault()
        alert('右边输入根据this.currentLeftUnit.value(' + this.currentLeftUnit.value +
              ')和this.currentRightUnit.value(' + this.currentRightUnit.value +
              ')搜索出转换参数，然后把this.currentLeftValue(' + this.currentLeftValue +
              ')和this.currentRightValue(' + this.currentRightValue +
              ')进行换算')
      }
    },
    components: {
      XHeader,
      Scroller
    }
  }
</script>



